<template>
    <div id="study" :style="pageStyle">
        <navigation class="nav"/>
        <div class="bookshelf">
            <div class="book-area" @click="selectBook('Java')">
                <div class="book"><book book-name="<div>JavaSE</div><div>源码解析</div>"/></div>
                <div class="support"><material width="8rem" height="1rem" thick="5rem"/></div>
            </div>
            <div class="book-area" @click="selectBook(2)">
                <div class="book"><book book-name="<div>Vue</div><div>搭建向导</div>"/></div>
                <div class="support"><material width="8rem" height="1rem" thick="5rem"/></div>
            </div>
            <div class="book-area" @click="selectBook('数据结构')">
                <div class="book"><book book-name="<div>数据结构</div><div>学习笔记</div>"/></div>
                <div class="support"><material width="8rem" height="1rem" thick="5rem"/></div>
            </div>
            <div class="book-area" @click="selectBook(4)">
                <div class="book"><book book-name="<div>SpringCloud</div><div>源码解析</div>"/></div>
                <div class="support"><material width="8rem" height="1rem" thick="5rem"/></div>
            </div>
            <div class="book-area" @click="selectBook(5)">
                <div class="book"><book book-name="<div>Mybatis</div><div>源码解析</div>"/></div>
                <div class="support"><material width="8rem" height="1rem" thick="5rem"/></div>
            </div>
        </div>
    </div>
</template>

<script>
    import navigation from "@/components/navigation_bar";
    import material from "@/components/material";
    import book from "@/components/book";
    import req from "@/utils/req";
    import api from "@/config/api";
    import Style from "@/utils/enery/Style";

    export default {
        data() {
            return {
                originX: "75%",
                originY: "50%"
            }
        },
        created() {
            document.addEventListener("keydown",this.onkeydown);
        },
        methods: {
            onkeydown(e) {
                if (e.code == "ArrowUp") {
                    if (this.originY.replace("%", "") >= 100) return;
                    this.originY = (Number(this.originY.replace("%", "")) + 10) + "%";
                }
                else if (e.code == "ArrowDown") {
                    if (this.originY.replace("%", "") <= 0) return;
                    this.originY = (Number(this.originY.replace("%", "")) - 10) + "%";
                }
                else if (e.code == "ArrowLeft") {
                    if (this.originX.replace("%", "") <= 0) return;
                    this.originX = (Number(this.originX.replace("%", "")) - 10) + "%";
                }
                else if (e.code == "ArrowRight") {
                    if (this.originX.replace("%", "") >= 100) return;
                    this.originX = (Number(this.originX.replace("%", "")) + 10) + "%";
                }
            },
            selectBook(label) {
                this.$router.push({name: "blog", query: label});
            }
        },
        computed: {
            pageStyle() {
                return new Style().perspectiveOrigin(this.originX, this.originY).value;
            }
        },
        components: {
            material, navigation, book
        },
        beforeRouteEnter(to, from, next) {
            window.document.body.classList.add("bookshelf-background");
            next();
        },
        beforeRouteLeave(to, from, next) {
            window.document.body.classList.remove("bookshelf-background");
            next();
        }
    }
</script>

<style lang="scss">
    .bookshelf-background {
        @include background("../../assets/img/b5.jpg", $width, $repeat);
    }
    #study {
        @include perspective(80rem);
    }
    #study {
        .bookshelf {
            @include align;
            @include wh(100%, icalc(100vh, $navH));
        }
        .book-area {
            width: 13rem;
            margin-bottom: -9rem;
            @include align(24, $column);
        }
        .book {
            margin-top: -4.5rem;
            @include transform(rotateX(10deg));
            @include transform-style(preserve-3d);
        }
    }
</style>
