<template xmlns:v-slot="http://www.w3.org/1999/xhtml">
    <div class="book-box">
        <div class="book">
            <material class="book-page-1" :width="pageW" :height="height" thick="1px" :color="coverColor" border-color="#A1724F">
                <template v-slot:front>
                    <div class="book-face" v-html="bookName"></div>
                </template>
            </material>
            <material class="book-page-2" :width="page2W" :height="page2H" thick="0.25rem" color="white" border-color="#AAA"/>
            <material class="book-page-3" :width="page3W" :height="page3H" thick="0.5rem" color="white" border-color="#AAA"/>
            <material class="book-page-4" :width="page2W" :height="page2H" thick="0.25rem" color="white" border-color="#AAA"/>
            <material class="book-page-5" :width="pageW" :height="height" thick="1px" :color="coverColor" border-color="#A1724F">
                <template v-slot:back>
                    <div class="book-back"></div>
                </template>
            </material>
            <div class="book-cover" :style="{'background-color': this.coverColor}"/>
        </div>
    </div>
</template>

<script>
    import material from "@/components/material";
    import Unit from "@/utils/enery/Unit";

    export default {
        props: {
            bookName: {
                request: true
            },
            width: {
                default: "5rem"
            },
            height: {
                default: "8rem"
            },
            coverColor: {
                default: "#A94F32"
            }
        },
        computed: {
            pageW() {
                return new Unit(this.width).add("0.25rem").value;
            },
            page2W() {
                return new Unit(this.width).sub("0.25rem").value;
            },
            page2H() {
                return new Unit(this.height).sub("0.5rem").value;
            },
            page3W() {
                return new Unit(this.width).sub("0.5rem").value;
            },
            page3H() {
                return new Unit(this.height).sub("0.5rem").value;
            }
        },
        components: {
            material
        }
    }
</script>

<style lang="scss">
    .book-box {
        div {
            @include transform-style(preserve-3d);
        }
        .book {
            position: relative;
            @include transform(translateZ(0.5rem));
        }
        .book > div {
            position: absolute;
        }
        .book-face {
            @include wh;
            @include align(1234, $column);
            @include font(0.3rem, white, 2rem);
        }
        .book-page-1 {
        }
        .book-page-2 {
            left: 0.125rem;
            @include transform(translateZ(-0.125rem));
        }
        .book-page-3 {
            @include transform(translateZ(-0.5rem));
        }
        .book-page-4 {
            left: 0.125rem;
            @include transform(translateZ(-0.875rem));
        }
        .book-page-5 {
            @include transform(translateZ(-1rem));
        }
        .book-cover {
            top: -4rem;
            left: -3rem;
            @include wh(1rem, 8rem);
            @include transform(rotateY(90deg) translateX(0.5rem));
        }
    }
</style>
